:root {
  --theme: #009688;
}

* {
  margin: 0;
  padding: 0;
}

body {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, sans-serif;
}

core-navbar {
  background: var(--theme);
  color: #fff;
  justify-content: space-between;
  font-size: x-large;
  display: flex;
}

core-navbar a {
  color: #fff;
  text-decoration: none;
  transition: all .2s;
  display: inline-block;
}

core-navbar a:hover, core-navbar a:active {
  transform: scale(1.4);
}

core-navbar > div {
  padding: 8px;
}

#modal {
  z-index: 1;
  width: 100%;
  height: 100%;
  background-color: #0006;
  animation: fade 1s forwards;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

@keyframes fade {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.modal-window {
  max-width: 50%;
  width: -moz-fit-content;
  width: fit-content;
  background-color: #fff;
  border-radius: 8px;
  margin: 80px auto 0;
  padding: 24px;
  position: absolute;
  left: 0;
  right: 0;
  box-shadow: 0 10px 20px #0003, 0 6px 6px #0003;
}

@media only screen and (max-width: 700px) {
  .modal-window {
    max-width: 75%;
  }
}

.modal-header {
  text-align: center;
  margin-bottom: 36px;
  font-size: 24px;
}

.modal-text {
  margin-bottom: 32px;
  margin-left: 16px;
  font-size: 16px;
  line-height: 20px;
}

#close-btn {
  color: #787c7e;
  float: right;
  margin-top: -16px;
  font-size: 32px;
}

#close-btn:hover, #close-btn:focus {
  color: #000;
  cursor: pointer;
  text-decoration: none;
}

